<!--  -->
<template>
  <div class="front-body">
    <el-card>
      <div slot="header" class="clearfix">
        <sui-breadcrumb></sui-breadcrumb>
      </div>
      <el-row>
        <el-col :span="8">
          <el-image v-if="songList.picture" style="width: 100%;height: 300px" :src="formatUrl(songList.picture)"></el-image>
        </el-col>
        <el-col :span="16" style="padding:0 10px;height: 300px;overflow-y: auto">
          <h1 style="text-align: center;margin: 0" class="font-shadow">{{songList.title}}</h1>
          <p class="font-shadow" v-html="songList.introduction"></p>
        </el-col>
      </el-row>
      <div v-if="dataList.length>0">
        <!--  -->
        <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList">
          <el-table-column label="id" prop="id" v-if="false"/>
          <el-table-column label="" type="index" fixed width="50"/>
          <el-table-column label="" prop="picture">
            <template slot-scope="scope">
              <el-image :src="formatUrl(scope.row.picture)" class="column-image"></el-image>
            </template>
          </el-table-column>
          <el-table-column label="" prop="name"/>
          <el-table-column label="" prop="singerId">
            <template slot-scope="scope">
              <span v-for="singer in singerDict" v-if="singer.id==scope.row.singerId">{{singer.name}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" align="center" width="250" fixed="right">
            <template slot-scope="scope">
              <el-button size="mini" type="success" title="" circle icon="el-icon-video-play" @click="play(scope.row)"></el-button>
              <el-button size="mini" type="primary" title="" @click="addPlay(scope.row)" circle plain icon="el-icon-plus"></el-button>
              <el-button size="mini" type="primary" title="" @click="collect(scope.row)" circle plain icon="el-icon-folder-add"></el-button>
              <el-button size="mini" type="info" title="" @click="downLoad(scope.row)" circle plain icon="el-icon-download"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-empty v-else class="sui-empty" description=""></el-empty>
    </el-card>
    <!--  -->
    <div id="aplayer"></div>
    <!--|-->
    <sui-evaluate v-if="queryForm.songSheetId" style="text-align: center;padding-top: 10px" :refId="queryForm.songSheetId" @success="success" type=""/>
    <!--|-->
    <user-evaluate style="margin:0 10px" v-if="queryForm.songSheetId" ref="userEvaluate" :refId="queryForm.songSheetId" type=""></user-evaluate>
  </div>
</template>

<script>
  import {mapActions} from "vuex";
  export default {
    data() {
      return {
        info: {
          fixed: false, // 
          listFolded: true, // 
          autoplay: true, // 
          preload: "auto", // 
          loop: "all", // all one none
          order: "list", //  list, random
        },
        total: 0, //
        dataList: [],
        singerDict: [],
        songPlayList: [],
        songList: {},
        queryForm: {
          current: 1,
          size: 100,
          songSheetId: "",
          songId: ""
        }
      };
    },
    mounted() {
      let params = this.$route.query;
      this.queryForm.songSheetId = params.songSheetId;
      //
      this.getList();
      //
      this.getSingerDict();
      //
      this.getSongList()
      this.scrollTop();
    },
    computed: {
      hasLogin() {
        return this.$store.getters.hasLogin;
      },
    },
    methods: {
      ...mapActions(['commitPlayList']),
      //
      collect(item) {
        if(!this.hasLogin){
          this.showWarn("");
          return;
        }
        this.$request.doPost('/api/message/subCollect', {type: "", content: item.name, refId: item.id}).then(res => {
          if (res.isOk) {
            this.showSuccess(res.info);
          }
        });
      },
      //
      play(item){
        if(!this.hasLogin){
          this.showWarn("");
          return;
        }
        const that = this;
        that.$request.doPost('/api/playList/sub', {songId: item.id}).then(res => {
          if (res.isOk) {
            that.commitPlayList();
            that.toPage({name:'songDetails',query:{id:item.id}})
          };
        });
      },
      //
      addPlay(item) {
        if(!this.hasLogin){
          this.showWarn("");
          return;
        }
        const that = this;
        that.$request.doPost('/api/playList/sub', {songId: item.id}).then(res => {
          if (res.isOk) {
            that.commitPlayList();
            that.showSuccess("");
          };
        });
      },
      downLoad(file) {
        if(!this.hasLogin){
          this.showWarn("");
          return;
        }
        //  a 
        const a = document.createElement('a');
        // 
        const event = new MouseEvent('click');
        //  a  download  name ''
        a.download = file.name || '';
        //  URL  a.href 
        a.href = "/api/sysAttach/downFile?refId=" + file.id;
        //  a 
        a.dispatchEvent(event);
        const loading = this.$loading({
          lock: false,
          text: ',...',
          spinner: 'el-icon-loading',
          background: 'rgba(232,244,255,0.25)'
        });
        setTimeout(function () {
          loading.close();
        }, 1500)
      },
      success() {
        this.$refs.userEvaluate.getList();
      },
      //
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //
      getList() {
        const that = this;
        this.$request.doGet('/api/listSong/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.list;
            // this.$store.commit('PLAY_LIST', this.dataList);
            this.total = res.list.length;
          }
        });
      },
      //
      getSongList() {
        this.$request.doGet('/api/songList/get?id=' + this.queryForm.songSheetId).then(res => {
          if (res.isOk) {
            this.songList = res.obj;
          }
        });
      },
      //
      getSingerDict() {
        this.$request.doGet('/api/singer/getList').then(res => {
          if (res.isOk) {
            this.singerDict = res.list;
          }
        });
      },
      //
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
    }
  };
</script>



