<!--  -->
<template>
  <div class="front-body">
    <el-card class="front-card">
      <div slot="header" class="clearfix">
        <sui-breadcrumb></sui-breadcrumb>
      </div>
      <el-row>
        <el-col :span="8">
          <el-image v-if="singer.picture" style="width: 100%;height: 300px" :src="formatUrl(singer.picture)"></el-image>
        </el-col>
        <el-col :span="16" style="padding:0 10px;height: 300px;overflow-y: auto">
          <h1 style="text-align: center;margin: 0;" class="font-shadow">{{singer.name}}</h1>
          <p class="font-shadow" v-html="singer.introduction"></p>
        </el-col>
      </el-row>
      <div v-if="dataList.length>0">
        <!--  -->
        <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList">
          <el-table-column label="id" prop="id" v-if="false"/>
          <el-table-column label="" type="index" fixed width="50"/>
          <el-table-column label="" prop="picture">
            <template slot-scope="scope">
              <el-image :src="formatUrl(scope.row.picture)" class="column-image"></el-image>
            </template>
          </el-table-column>
          <el-table-column label="" prop="name"/>
          <el-table-column label="" prop="singerId">
            <template slot-scope="scope">
              <span v-for="singer in singerDict" v-if="singer.id==scope.row.singerId">{{singer.name}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" align="center" width="250" fixed="right">
            <template slot-scope="scope">
              <el-button size="mini" type="success" title="" circle icon="el-icon-video-play" @click="play(scope.row)"></el-button>
              <el-button size="mini" type="primary" title="" @click="addPlay(scope.row)" circle plain icon="el-icon-plus"></el-button>
              <el-button size="mini" type="primary" title="" @click="collect(scope.row)" circle plain icon="el-icon-folder-add"></el-button>
              <el-button size="mini" type="info" title="" @click="downLoad(scope.row)" circle plain icon="el-icon-download"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <div class="pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :background="true"
            :current-page="queryForm.current"
            :page-sizes="[5,10,20,30,50]"
            :page-size="queryForm.size"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>
      <el-empty v-else class="sui-empty" description=""></el-empty>
    </el-card>
  </div>
</template>

<script>
  import {mapActions} from "vuex";

  export default {
    name: "singerForm",
    data() {
      return {
        total: 0, //
        dataList: [],
        singerDict: [],
        queryForm: {
          current: 1,
          size: 5,
          singerId: ""
        },
        //
        singer: {
          picture: "",
          name: "",
          sex: "",
          regionArea: "",
          introduction: "",
        },
        //
        rules: {
          picture: [{required: true, message: '', trigger: 'change'}],
          name: [{required: true, message: '', trigger: 'blur'},{validator:this.$validator.unique,form:this,model:'singer',tableName:'singer',column:'name',message:'',trigger:'blur'}],
          sex: [{required: true, message: '', trigger: 'change'}],
          regionArea: [{required: true, message: '', trigger: 'change'}],
          introduction: [{required: true, message: '', trigger: 'blur'}],
        }
      }
    },
    computed: {
      hasLogin() {
        return this.$store.getters.hasLogin;
      },
      //
      sexDict() {
        return this.$store.getters.getDictArray('sys_sex');
      },
      //
      regionAreaDict() {
        return this.$store.getters.getDictArray('region_area');
      },
    },
    mounted() {
      let singerId = this.$route.query.id;
      this.queryForm.singerId = singerId;
      //
      this.getList()
      //
      this.getSinger(singerId);
      //
      this.listSong();
      //
      this.getSingerDict();

    },
    methods: {
      ...mapActions(['commitPlayList']),
      //id
      getSinger(id) {
        this.$request.doGet("/api/singer/get?id=" + id).then(res => {
          if (res.isOk) {
            this.singer = res.obj;
          }
        });
      },
      //
      collect(item) {
        if(!this.hasLogin){
          this.showWarn("");
          return;
        }
        this.$request.doPost('/api/message/subCollect', {type: "", content: item.name, refId: item.id}).then(res => {
          if (res.isOk) {
            this.showSuccess(res.info);
          }
        });
      },
      //
      play(item){
        if(!this.hasLogin){
          this.showWarn("");
          return;
        }
        this.toPage({name:'songDetails',query:{id:item.id}})
      },
      //
      addPlay(item) {
        if(!this.hasLogin){
          this.showWarn("");
          return;
        }
        const that = this;
        that.$request.doPost('/api/playList/sub', {songId: item.id}).then(res => {
          if (res.isOk) {
            that.commitPlayList();
            that.showSuccess("");
          };
        });
      },
      downLoad(file) {
        if(!this.hasLogin){
          this.showWarn("");
          return;
        }
        //  a 
        const a = document.createElement('a');
        // 
        const event = new MouseEvent('click');
        //  a  download  name ''
        a.download = file.name || '';
        //  URL  a.href 
        a.href = "/api/sysAttach/downFile?refId=" + file.id;
        //  a 
        a.dispatchEvent(event);
        const loading = this.$loading({
          lock: false,
          text: ',...',
          spinner: 'el-icon-loading',
          background: 'rgba(232,244,255,0.25)'
        });
        setTimeout(function () {
          loading.close();
        }, 1500)
      },
      success() {
        this.$refs.userEvaluate.listSong();
      },
      //
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //
      getList() {
        const that = this;
        this.$request.doGet('/api/song/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        });
      },
      //
      getSingerDict() {
        this.$request.doGet('/api/singer/getList').then(res => {
          if (res.isOk) {
            this.singerDict = res.list;
          }
        });
      },
      //
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
    }
  }
</script>

<style scoped>

</style>



