<template>
  <el-button :type="type" :icon="icon" :disabled="disabled" :size="size" @click="exportData">{{title}}</el-button>
</template>
<script>
  import qs from 'qs'
  export default {
    props: {
      title: {
        type: String,
        default: ''
      },
      action: {
        type: String,
        default: ''
      },
      disabled: {
        type: Boolean,
        default: false
      },
      dataList: {
        type: Array,
        default: []
      },
      type: {
        type: String,
        default: 'primary'
      },
      icon: {
        type: String,
        default: 'el-icon-download'
      },
      size: {
        type: String,
        default: 'mini'
      }
    },
    mounted() {
    },
    methods:{
      //
      exportData() {
        const that = this;
        let dataList = this.dataList;
        if(dataList.length==0 && that.$parent.$parent.$parent.dataList && that.$parent.$parent.$parent.dataList.length==0){
          that.showWarn("");
          return;
        }
        let queryForm = that.$parent.$parent.$parent.queryForm;
        let paramsStr = qs.stringify(queryForm,{allowDots : true,skipNulls: true});//postqs
        that.showConfirm('?', function () {
          //  a 
          const a = document.createElement('a');
          // 
          const event = new MouseEvent('click');
          //  URL  a.href 
          a.href = that.action+"?"+paramsStr;
          //  a 
          a.dispatchEvent(event);
          //location.href = that.action;
          const loading = that.$loading({
            lock: false,
            text: ',...',
            spinner: 'el-icon-loading',
            background: 'rgba(232,244,255,0.25)'
          });
          setTimeout(function () {
            loading.close();
          },1500)
        });
      },
    }
  }
</script>



