<!--  -->
<template>
  <div class="body">
    <el-dialog width="500px" class="front-dialog" center :title="dialog.title" :close-on-click-modal="false" :append-to-body="true" v-dialog-drag :visible.sync="dialog.open">
      <el-form :model="audit" ref="auditForm" :rules="rules" label-width="100px">
        <el-descriptions class="margin-top" :column="1" direction="vertical">
          <el-descriptions-item label="">
            <el-radio-group v-model="audit.status" size="small" style="text-align: center">
              <el-radio :label=true border></el-radio>
              <el-radio :label=false border></el-radio>
            </el-radio-group>
          </el-descriptions-item>
          <el-descriptions-item label="">
            <el-input type="textarea" v-model="audit.remarks" placeholder=""/>
          </el-descriptions-item>
        </el-descriptions>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "auditForm",
    data() {
      return {
        status: '1',
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        audit: {
          row:{},
          status:true,
          remarks: ""
        },
        //
        rules: {
          status: [{required: true, message: '', trigger: 'blur'}],
          remarks: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    methods: {
      //
      open(option) {
        this.showDialog(option.title,option.row);
        this.audit.status = true;
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //
      submitForm: function () {
        if(this.audit.remarks==''){
          this.showWarn("")
        }else {
          this.$emit('auditResponse',this.audit)
        }
      },
      //
      showDialog(title,row) {
        this.resetForm(this.$refs.auditForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.audit.row = row;//
      }
    }
  }
</script>




