<!--  -->
<template>
  <el-dialog width="420px" class="front-dialog" center :title="dialog.title" :close-on-click-modal="false" :append-to-body="true" v-dialog-drag :visible.sync="dialog.open">
    <el-form :model="user" ref="passwordForm" :rules="rules" label-width="100px">
      <el-form-item label="" prop="loginName">
        <el-input v-model="user.loginName" disabled placeholder="" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="oldPassword">
        <el-input v-model="user.oldPassword" type="password" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="newPassword">
        <el-input v-model="user.newPassword" type="password" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="prePassword">
        <el-input v-model="user.prePassword" type="password" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
    </el-form>
    <div class="dialog-footer">
      <el-button class="sub-btn" @click="submitForm('passwordForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    name: "userPassword",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        user: {
          loginName:"",
          oldPassword: "",
          newPassword: "",
          prePassword: ""
        },
        //
        rules: {
          loginName: [{required: true, message: '', trigger: 'blur'}],
          oldPassword: [{required: true, message: '', trigger: 'blur'}],
          newPassword: [{required: true, message: '', trigger: 'blur'}],
          prePassword: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    methods: {
     //
     open(option) {
       const pageFrom = option.pageFrom;
       const disabled = pageFrom == 'view' ? true : false;
       this.showDialog(option.title, disabled);
       this.getLoginInfo();
     },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      getLoginInfo() {
        this.$request.doGet("/api/register/getLoginInfo").then(res => {
          if (res.isOk) {
            this.user = res.data.userInfo;
          }
        });
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            if(this.user.newPassword!=this.user.prePassword){
              this.showWarn("");
              return
            }
            that.$request.doPost('/api/register/modifyPsd', that.user).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess(res.info);
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.passwordForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>
<style scoped lang="scss">
  @import '../../../../assets/styles/theme';
  .front-dialog /deep/ .el-dialog__headerbtn .el-dialog__close{
    color: white!important;
  }
  .front-dialog /deep/ .el-dialog__header  {
    background: $theme-front-color;
  }
  .front-dialog /deep/ .el-dialog__title  {
    font-weight: bold;
    color: white!important;
  }

  .sub-btn  {
    margin: 20px 0 10px;
    width: 100%;
    background: $theme-front-color;
    color: white;
    height: 45px
  }
</style>



