<template>
    <el-dialog width="600px" center :title="file.name" :visible.sync="open" :append-to-body="true" v-dialogDrag>
      <video-player
        :title="file.src"
        v-if="open"
        class="video-player vjs-custom-skin"
        ref="videoPlayer"
        :playsinline="true"
        :options="playerOptions"
      >
      </video-player>
    </el-dialog>
</template>
<script>
  //
  export default {
    props: {
      title: {
        type: String,
        default: ''
      },
      src: {
        type: String,
        default: ''
      },
    },
    data(){
      return{
        file:{
          name:"",
          src:""
        },
        //
        open: false,
        playerOptions: {
          playbackRates: [0.5, 1.0, 1.5, 2.0],//
          autoplay: false,//
          muted: false,//
          loop: false,//
          preload: "auto",
          language: "zh-CN", //
          aspectRatio: "16:9",//
          fluid: true,
          sources: [
            {
              type: "",
              src: "", //url
            },
          ],
          poster: "", //
          notSupportedMessage: "",
          controlBar: {
            timeDivider: true,
            durationDisplay: true,//
            remainingTimeDisplay: false,//
            fullscreenToggle: true,//
          },
        },
      }
    },
    mounted() {
    },
    methods:{
      play(file) {
        this.open=true;
        this.file=file;
        this.playerOptions.sources[0].src=file.src;
      },
    }
  }
</script>



