import Vue from 'vue'
import NProgress from 'nprogress'
import Router from 'vue-router'
import $store from '@/js/vuex/store'
Vue.use(Router);
const router = new Router({
  mode:'history',
  routes: [
    //404
    {path: '/404', component: (resolve) => require(['@/pages/admin/sys/error/404.vue'], resolve)},
    //admin
    {meta: {title: ""}, name: 'index', path: '', redirect: '/login', hidden: true},//
    {meta: {title: ""}, name: 'login', path: '/login', component: (resolve) => require(['@/pages/admin/module/login/login.vue'], resolve),},
    {
      meta: {title: ""}, name: 'adminHome', path: '/main/home', component: (resolve) => require(['@/pages/admin/module/main/home.vue'], resolve),
      children: [
        {meta: {title: ""}, name: "adminIndex", path: '/main/index',component: (resolve) => require(['@/pages/admin/module/main/index.vue'], resolve),},
      ]
    },
    //front
    {meta: {title: ""}, name: 'frontIndex', path: '/front', component: (resolve) => require(['@/pages/front/index/index.vue'], resolve),
      children: [
        {meta: {title: ""}, name: 'frontHome', path: '/front/home', component: (resolve) => require(['@/pages/front/service/home/home.vue'], resolve)},
        {meta: {title: ""}, name: 'noticeDetails', path: '/front/noticeDetails', component: (resolve) => require(['@/pages/front/service/notice/noticeDetails.vue'], resolve)},
        {meta: {title: ""}, name: 'categoryList', path: '/front/categoryList', component: (resolve) => require(['@/pages/front/service/category/categoryList.vue'], resolve)},
        {meta: {title: ""}, name: 'recommendList', path: '/front/recommendList', component: (resolve) => require(['@/pages/front/service/song/recommendList.vue'], resolve)},
        {meta: {title: ""}, name: 'singerList', path: '/front/singerList', component: (resolve) => require(['@/pages/front/service/singer/singerList.vue'], resolve)},
        {meta: {title: ""}, name: 'singerDetails', path: '/front/singerDetails', component: (resolve) => require(['@/pages/front/service/singer/singerDetails.vue'], resolve)},
        {meta: {title: ""}, name: 'userSongList', path: '/front/userSongList', component: (resolve) => require(['@/pages/front/service/song/userSongList.vue'], resolve)},
        {meta: {title: ""}, name: 'listSongList', path: '/front/listSongList', component: (resolve) => require(['@/pages/front/service/listSong/listSongList.vue'], resolve)},
        {meta: {title: ""}, name: 'searchSongList', path: '/front/searchSongList', component: (resolve) => require(['@/pages/front/service/song/searchSongList.vue'], resolve)},
        {meta: {title: ""}, name: 'songDetails', path: '/front/songDetails', component: (resolve) => require(['@/pages/front/service/song/songDetails.vue'], resolve)},
      ]
    },
  ]
});

//
function generateRoutes(accessRoutes) {
  let routers = router.options['routes'];
  routers.forEach(item => {
    if (item.name === 'adminHome') {
      let userRoutes = accessRoutes.map(item => {
        return {
          name: item.name,
          path: item.path,
          meta: {title: item.title},
          component: (resolve) => require(["@/pages/" + item.component], resolve),
        }
      });
      item.children.push(...userRoutes);
      console.log("userRoutes", userRoutes);
      routers.push({path: '*', redirect: '/404', hidden: true})
      router.addRoutes(routers);//,
      console.log("allRoutes", routers);
    }
  })
}

//
router.beforeEach((to, from, next) => {
  console.log("to:", to);
  NProgress.start();
  /**************************************** admin ****************************************/
  if (to.path.indexOf('/front') < 0) {
    if (to.path != "/login") {
      if ($store.getters.routes.length == 0) {//
        $store.dispatch('commitConfig');
        $store.dispatch('commitDictList');
        $store.dispatch('commitAdminLoginInfo').then(res => {
          if ($store.getters.hasLogin) {//
            $store.dispatch('commitUserRoutes').then(res => {
              let accessRoutes = res.list;
              generateRoutes(accessRoutes);
              next({...to, replace: true}) // hack addRoutes
            }).catch(err => {
              console.log("err:", to)
              $store.dispatch('commitLoginOut').then(() => {
                next({path: '/login'})
                NProgress.done();
              })
            })
          } else {//
            $store.dispatch('commitLoginOut').then(() => {
              next({path: '/login'});
              NProgress.done();
            });
          }
        });
      } else {//
        next();
        NProgress.done();
      }
    } else {
      $store.dispatch('commitAdminLoginInfo').then(res => {
        if ($store.getters.hasLogin) {//
          next({path: '/main/home'});
          NProgress.done();
        } else {
          next();
          NProgress.done();
        }
      });
    }
  }
  /**************************************** front ****************************************/
  if (to.path.indexOf('/front') >= 0) {
    if ($store.getters.dictList.length == 0) {//
      $store.dispatch('commitConfig');//
      $store.dispatch('commitDictList');
      if (sessionStorage.getItem("token")) {//
        $store.dispatch('commitFrontLoginInfo');
      } else {
        $store.dispatch('commitFrontLoginOut')
      }
    }
    next();
    NProgress.done();
  }
});

router.afterEach(() => {
  document.title = "";
  if ($store.getters.config.projectName) {
    document.title = $store.getters.config.projectName;
  }
  NProgress.done();
});
export default router



