<!--  -->
<template>
  <el-dialog width="420px" class="front-dialog" center :title="dialog.title" :close-on-click-modal="false" :append-to-body="true" v-dialog-drag :visible.sync="dialog.open">
    <el-form :model="register" ref="registerForm" :rules="rules" label-width="100px">
      <el-form-item label="" prop="loginName">
        <sui-photo type="user" v-model="register.photo" :disabled="dialog.disabled" :url="register.photo"></sui-photo>
      </el-form-item>
      <el-form-item label="" prop="loginName">
        <el-input v-model="register.loginName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="userName">
        <el-input v-model="register.userName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="register.phone" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="register.email" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
    </el-form>
    <div class="dialog-footer">
      <el-button class="sub-btn" @click="submitForm('registerForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import {mapActions} from "vuex";
  export default {
    name: "userInfo",
    data() {
      return {
        register: {
          photo: "",
          loginName: "",
          userName: "",
          email: "",
          phone: "",
        },
        roleList: [],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        rules: {
          photo: [{required: true, message: '', trigger: 'blur'}],
          loginName: [{required: true, message: '', trigger: 'blur'},{validator: this.$validator.unique, form: this, tableName: 'register', column: 'login_name', model: 'register', message: '', trigger: 'blur'}],
          email: [{type: 'email', message: '', trigger:'blur'},{validator:this.$validator.unique,form:this,tableName:'register',column:'email',model:'register',message: '',trigger: 'blur'}],
          phone: [{validator: this.$validator.phone, message: '11', trigger: 'blur'},{validator: this.$validator.unique, form: this,model: 'register', tableName: 'register', column: 'phone',  message: '', trigger: 'blur'}],
          idCard: [{validator:this.$validator.idCard, message: '', trigger: 'blur'},{validator: this.$validator.unique, form: this,model: 'register', tableName: 'register', column: 'id_card',  message: '', trigger: 'blur'}]
        }
      }
    },
    methods: {
      ...mapActions(['commitFrontUserInfo']),
      //
      open(option) {
        this.showDialog(option.title, false);
        this.getLoginInfo();
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      getLoginInfo() {
        this.$request.doGet("/api/register/getLoginInfo").then(res => {
          if (res.isOk) {
            this.register = res.data.userInfo;
          }
        });
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.commitFrontUserInfo(that.register).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
              }
            })
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.registerForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>
<style scoped lang="scss">
  @import '../../../../assets/styles/theme';
  .front-dialog /deep/ .el-dialog__headerbtn .el-dialog__close{
    color: white!important;
  }
  .front-dialog /deep/ .el-dialog__header  {
    background: $theme-front-color;
  }
  .front-dialog /deep/ .el-dialog__title  {
    font-weight: bold;
    color: white!important;
  }

  .sub-btn  {
    margin: 20px 0 10px;
    width: 100%;
    background: $theme-front-color;
    color: white;
    height: 45px
  }
</style>



